<template>
	<div id="projectdet-page">
		<div
			class="projectdet-thumb"
			:style="{ backgroundImage: 'url('+require('../assets/img/project/'+project_item.thumb)+')' }">
		</div>
		<div class="projectdet container">
			<h3>{{project_item.title}}</h3>
			<p v-if="project_item.github_name">
				<small v-if="project_item.github.stargazers_count || project_item.github.forks_count">
					<i class="far fa-star"></i> {{project_item.github.stargazers_count}}
					<!-- <small><i class="far fa-clock"></i> {{pr.year}}</small> -->
					<svg aria-label="forks" class="svg-icon" viewBox="0 0 16 16" version="1.1" width="16" height="16" role="img"><path fill-rule="evenodd" d="M5 3.25a.75.75 0 11-1.5 0 .75.75 0 011.5 0zm0 2.122a2.25 2.25 0 10-1.5 0v.878A2.25 2.25 0 005.75 8.5h1.5v2.128a2.251 2.251 0 101.5 0V8.5h1.5a2.25 2.25 0 002.25-2.25v-.878a2.25 2.25 0 10-1.5 0v.878a.75.75 0 01-.75.75h-4.5A.75.75 0 015 6.25v-.878zm3.75 7.378a.75.75 0 11-1.5 0 .75.75 0 011.5 0zm3-8.75a.75.75 0 100-1.5.75.75 0 000 1.5z"></path></svg>
					{{project_item.github.forks_count}}
				</small>
				<small><i class="fa fa-clock mx-1"></i><timeago :datetime="new Date(project_item.github.created_at)"></timeago></small>
			</p>
			<p>{{project_item.desc}}</p>
			<p>
				<a :href="project_item.link" class="text-white" title="View in project page" target="_blank"><i class="fas fa-paper-plane mr-2"></i> View Project</a>
			</p>
			<vue-markdown :source="content_item" class="vue-markdown">
			</vue-markdown>

			<p class="text-center my-4">
				<small>Created By Putu Jhonarendra. Pageviews: {{pageview.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")}}</small>
			</p>
			<ButtonToTop />
		</div>
	</div>
</template>

<script>
import axios from 'axios'
import viewCounter from './../mixins/viewCounter'
import VueMarkdown from 'vue-markdown'
import '../assets/css/projectdet.css'
import prj_data from '../data/project.json'
import git_data from '../data/repos.json'
import ButtonToTop from '../components/ButtonToTop'

export default {
	data(){
		return {
			project_item: {},
			is_online: false,
			content_item: ''
		}
	},
	components: { VueMarkdown, ButtonToTop },
	mixins: [viewCounter],
	methods:{
		initializeProject(){
			this.is_online = navigator.onLine

			let slug = this.$route.params.slug
			let show_project

			prj_data.forEach((e) => {
				if(slug == e.slug){
					show_project = e
				}
			})
			if(show_project.github_name){
				// is github public project
				if(this.is_online){
					this.getGithubData(show_project)
				} else {
					git_data.forEach((e) => {
						if(e.name == show_project.github_name){
							show_project.github = e
						}
					})
					this.project_item = show_project
				}
			} else {
				// is github private project
				this.project_item = show_project
			}
		},
		getGithubData(show_project){
			axios
			.get('https://api.github.com/users/jhonarendra/repos')
			.then(res => {
				let repos = JSON.parse(JSON.stringify(res.data))

				repos.forEach((e) => {
					if(e.name == show_project.github_name){
						show_project.github = e
					}
				})
				this.project_item = show_project

				axios
				.get('https://raw.githubusercontent.com/'+this.project_item.github.full_name+'/master/README.md')
				// .get('https://raw.githubusercontent.com/jhonarendra/android-restoran/master/README.md')
				.then(res => {
					this.content_item = res.data
				})
			})
		}
	},
	mounted(){
		// this.updatePageview() // call mixins
		this.initializeProject()
	}
}
</script>